.body {
  > div:first-child {
    height: 80px;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #444;
    > div:first-child {
      display: flex;
      align-items: center;
      > img {
        width: 60px;
        height: 60px;
        border-radius: 10px;
      }
      > p {
        font-size: 1.5rem;
        margin-left: 10px;
        color: #fff;
      }
    }
    > div:last-child {
      display: flex;
      align-items: center;
      > * + * {
        margin-left: 10px;
      }
      > a {
        font-size: 1.2rem;
        text-decoration: none;
        color: #fff;
      }
    }
  }
  > div:last-child {
    position: relative;
    display: flex;
    > div:first-child {
      position: relative;
      width: 250px;
      background-color: #acacac;
      padding: 20px;
      height: calc(100vh - 80px - 40px);
      > div {
        padding: 0;
        list-style: none;
        > div {
          > p {
            font-size: 12px;
            color: #fff;
          }
        }
      }
    }
    > div:last-child {
      padding: 20px;
      width: 100%;
      flex: 1;
      > * + * {
        margin-top: 20px;
      }
      > div {
        padding: 20px;
        border: 2px solid #444444;
        > div {
          > h1 {
            margin: 0px;
          }
        }
        &:last-child {
          div:last-child {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 300px;
            height: 50px;
            border: none;
            background-image: linear-gradient(to right, red, orange, yellow, green, blue, indigo, purple);
            border-radius: 15px;
            > p {
              color: #fff;
              text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
            }
          }
        }
      }
    }
  }
}
